div, body {
  margin:0;
  padding:0;
}

.center {
  text-align :center; 
  margin-left:auto;
  margin-right:auto; 
}

/* =============== dropdown ===============*/

.dropdown {
  float: right;
  overflow: hidden;
  margin: 0px 10px;
  background-color: inherit;
}

.dropdown .dropbtn {
  border: none;
  color:inherit;
  background-color: inherit;
  font-size:inherit;
}

.dropdown:hover .dropbtn, .dropbtn:hover {
  color: rgb(250, 235, 153);
}

.dropdown-content {
  background-color: inherit;
  display: none;
  position: absolute;
  -webkit-box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
          box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
  background-color: inherit;
  float: none;
  padding: 10px 10px;
  text-decoration: none;
  display: block;
  background-color: inherit;
}

.dropdown:hover .dropdown-content {
  display: block;
}

