/* ================ sidenav ===============*/

#sidenav , #sidebar {
  margin:0;
  width: 200px;
  height: 100vh;
  float:left;
  top: 0;
  left: 0;
  overflow-x: hidden;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  padding-top: 20px;
  color:white;
  background-color:#336699;
}

#sidebar {
  position: fixed;
  top: 30px;
  white-space:nowrap;
}

#sidenav a {
  padding: 8px 16px;
  text-decoration: none;
  font-size: large;
  display: block;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  color: #dddddd;
}

